import { IFilterState } from '@/store/modules/filter/state'
import { MutationTree } from 'vuex'
import { IFilterForm, IFilterItem, IFilterGrouped, IFilterTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_FORM_EVENT = 'loadFormEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
  LOAD_GROUPED_EVENT = 'loadGroupedEvent'
}

export const mutations: MutationTree<IFilterState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IFilterState, data: IResponseTable<IFilterTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IFilterState, data: IFilterItem[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_GROUPED_EVENT]: (state: IFilterState, data: IFilterGrouped[]) => {
    state.grouped = data
  },

  [mutationTypes.LOAD_FORM_EVENT]: (state: IFilterState, data: IFilterForm) => {
    state.dataForm = data
  }
}
