import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IProductStatusForm,
  IProductStatusItem,
  IProductStatusTableItem,
  IResponseTable
} from '@/types'

class ProductStatusApi {
  getById (id: number): Promise<AxiosResponse<IProductStatusForm>> {
    return httpClient.get(`/product-status/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IProductStatusItem[]>> {
    return httpClient.get('/product-status', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IProductStatusTableItem>>> {
    return httpClient.post('/product-status/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IProductStatusForm): Promise<AxiosResponse<IProductStatusForm>> {
    return httpClient.post('/product-status', payload)
  }

  update (id: number, payload: IProductStatusForm): Promise<AxiosResponse<IProductStatusForm>> {
    return httpClient.patch(`/product-status/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/product-status', {
      ids
    })
  }
}

export default new ProductStatusApi()
