import { ICountryState } from '@/store/modules/country/state'
import { MutationTree } from 'vuex'
import { ICountryItem, ICountryTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<ICountryState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: ICountryState, data: IResponseTable<ICountryTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: ICountryState, data: ICountryItem[]) => {
    state.items = data
  }
}
