import { ISystemState } from '@/store/modules/system/state'
import { MutationTree } from 'vuex'

export enum mutationTypes {
  SET_SNACKBAR = 'setSnackbar',
}

export const mutations: MutationTree<ISystemState> = {
  [mutationTypes.SET_SNACKBAR]: (state, data: string | null) => {
    state.snackbar = data
  }
}
