import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IVoucherForm,
  IVoucherItem,
  IVoucherTableItem,
  IResponseTable
} from '@/types'

class VoucherApi {
  getById (id: number): Promise<AxiosResponse<IVoucherForm>> {
    return httpClient.get(`/voucher/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IVoucherItem[]>> {
    return httpClient.get('/voucher', payload)
  }

  getTableMain (payload: any = {}): Promise<AxiosResponse<IResponseTable<IVoucherTableItem>>> {
    return httpClient.post('/voucher/paginate', payload, {
      hideNotification: true
    })
  }

  getTableHistory (id: number, payload: any = {}): Promise<AxiosResponse<IResponseTable<IVoucherTableItem>>> {
    return httpClient.post(`/voucher/${id}/history/paginate`, payload, {
      hideNotification: true
    })
  }

  create (payload: IVoucherForm): Promise<AxiosResponse<IVoucherForm>> {
    return httpClient.post('/voucher', payload)
  }

  update (id: number, payload: IVoucherForm): Promise<AxiosResponse<IVoucherForm>> {
    return httpClient.patch(`/voucher/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/voucher', {
      ids
    })
  }
}

export default new VoucherApi()
