import { useStore } from 'vuex'
import { actionTypes } from '@/store/modules/user/actions'

export const TOKEN_KEY = 'accessToken'

export default class TokenResolver {
  static initToken () {
    const { dispatch } = useStore()

    const accessToken = localStorage.getItem(TOKEN_KEY)
    if (accessToken) {
      dispatch(`user/${actionTypes.SET_TOKEN}`, accessToken)
    }
  }

  static setToken (accessToken: string) {
    return localStorage.setItem(TOKEN_KEY, accessToken)
  }

  static removeToken () {
    localStorage.removeItem(TOKEN_KEY)
  }
}
