
import { useStore } from 'vuex'
import { actionTypes } from '@/store/modules/system/actions'
import { computed } from 'vue'

export default {
  name: 'VNotification',
  setup () {
    const { dispatch, state } = useStore()
    const hideSnackbar = () => dispatch(`system/${actionTypes.HIDE_SNACKBAR}`)
    const snackbar = computed(() => state.system.snackbar)
    return {
      snackbar,
      hideSnackbar
    }
  }
}
