import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IReturnActionForm,
  IReturnActionItem,
  IReturnActionTableItem,
  IResponseTable
} from '@/types'

class ReturnActionApi {
  getById (id: number): Promise<AxiosResponse<IReturnActionForm>> {
    return httpClient.get(`/return-action/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IReturnActionItem[]>> {
    return httpClient.get('/return-action', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IReturnActionTableItem>>> {
    return httpClient.post('/return-action/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IReturnActionForm): Promise<AxiosResponse<IReturnActionForm>> {
    return httpClient.post('/return-action', payload)
  }

  update (id: number, payload: IReturnActionForm): Promise<AxiosResponse<IReturnActionForm>> {
    return httpClient.patch(`/return-action/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/return-action', {
      ids
    })
  }
}

export default new ReturnActionApi()
