import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import { loadFonts } from './plugins/webfontloader'
import i18n from '@/languages'
import Sparkline from 'vue-sparklines'
import Vue3EasyDataTable from 'vue3-easy-data-table'
import 'vue3-easy-data-table/dist/style.css'
import 'v-calendar/dist/style.css'
import VueMask from '@devindex/vue-mask'
import CKEditor from '@ckeditor/ckeditor5-vue'
import Notifications from '@kyvg/vue3-notification'
import * as ConfirmDialog from 'vuejs-confirm-dialog'
import VConfirm from '@/components/VConfirm.vue'
import './styles/global.scss'
import VCalendarLibrary from 'v-calendar'
import 'element-plus/dist/index.css'
import ElementPlus from 'element-plus'
import ElImageInput from '@/components/ElImageInput.vue'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'
// import ru from 'element-plus/dist/locale/ru.mjs'

loadFonts()

const app = createApp(App)
  .use(router)
  .use(store)
  .use(vuetify)
  .use(i18n)
  .use(Notifications)
  .use(ConfirmDialog)
  .use(VCalendarLibrary)
  .use(ElementPlus, {
    size: 'large'
    // locale: ru
  })
  .use(Sparkline)
  .use(VueMask)
  .use(CKEditor)
  .component('el-image-input', ElImageInput)
  .component('v-confirm', VConfirm)
  .component('v-data-table', Vue3EasyDataTable)

for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

export default app.mount('#app')
