import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  ILengthClassForm,
  ILengthClassItem,
  ILengthClassTableItem,
  IResponseTable
} from '@/types'

class LengthClassApi {
  getById (id: number): Promise<AxiosResponse<ILengthClassForm>> {
    return httpClient.get(`/length-class/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<ILengthClassItem[]>> {
    return httpClient.get('/length-class', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<ILengthClassTableItem>>> {
    return httpClient.post('/length-class/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: ILengthClassForm): Promise<AxiosResponse<ILengthClassForm>> {
    return httpClient.post('/length-class', payload)
  }

  update (id: number, payload: ILengthClassForm): Promise<AxiosResponse<ILengthClassForm>> {
    return httpClient.patch(`/length-class/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/length-class', {
      ids
    })
  }
}

export default new LengthClassApi()
