import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import { RequestLoginValue, ResponseLoginValue, ResponseProfileValue } from '@/types'

const AuthService = {
  login: (data: RequestLoginValue): Promise<AxiosResponse<ResponseLoginValue>> =>
    httpClient.post('/auth/login', data),

  profile: (): Promise<AxiosResponse<ResponseProfileValue>> =>
    httpClient.get('/profile')
}

export default AuthService
