export interface IUser {
  id: number;
  username: string;
  email: string;
}

export interface IAuthState {
  auth: boolean;
  token: string | null;
  user: IUser;
}

export const initialState = (): IAuthState => {
  return {
    auth: false,
    token: null,
    user: {
      id: 0,
      username: '',
      email: ''
    }
  }
}
