import { IReturnReasonState } from '@/store/modules/return-reason/state'
import { MutationTree } from 'vuex'
import { IReturnReasonItem, IReturnReasonTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IReturnReasonState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IReturnReasonState, data: IResponseTable<IReturnReasonTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IReturnReasonState, data: IReturnReasonItem[]) => {
    state.items = data
  }
}
