import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IAttributeGroupForm,
  IAttributeGroupItem,
  IAttributeTableItem,
  IResponseTable
} from '@/types'

class AttributeGroupApi {
  getAll (payload: any = {}): Promise<AxiosResponse<IAttributeGroupItem[]>> {
    return httpClient.get('/attribute-group', payload)
  }

  getById (id: number): Promise<AxiosResponse<IAttributeGroupForm>> {
    return httpClient.get(`/attribute-group/${id}`)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IAttributeTableItem>>> {
    return httpClient.post('/attribute-group/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IAttributeGroupForm): Promise<AxiosResponse<IAttributeGroupForm>> {
    return httpClient.post('/attribute-group', payload)
  }

  update (id: number, payload: IAttributeGroupForm): Promise<AxiosResponse<IAttributeGroupForm>> {
    return httpClient.patch(`/attribute-group/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/attribute-group', {
      ids
    })
  }
}

export default new AttributeGroupApi()
