import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IManufacturerForm,
  IManufacturerItem,
  IManufacturerTableItem,
  IResponseTable
} from '@/types'

class ManufacturerApi {
  getById (id: number): Promise<AxiosResponse<IManufacturerForm>> {
    return httpClient.get(`/manufacturer/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IManufacturerItem[]>> {
    return httpClient.get('/manufacturer', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IManufacturerTableItem>>> {
    return httpClient.post('/manufacturer/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IManufacturerForm): Promise<AxiosResponse<IManufacturerForm>> {
    return httpClient.post('/manufacturer', payload)
  }

  update (id: number, payload: IManufacturerForm): Promise<AxiosResponse<IManufacturerForm>> {
    return httpClient.patch(`/manufacturer/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/manufacturer', {
      ids
    })
  }
}

export default new ManufacturerApi()
