import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IOrderStatusForm,
  IOrderStatusItem,
  IOrderStatusTableItem,
  IResponseTable
} from '@/types'

class OrderStatusApi {
  getById (id: number): Promise<AxiosResponse<IOrderStatusForm>> {
    return httpClient.get(`/order-status/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IOrderStatusItem[]>> {
    return httpClient.get('/order-status', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IOrderStatusTableItem>>> {
    return httpClient.post('/order-status/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IOrderStatusForm): Promise<AxiosResponse<IOrderStatusForm>> {
    return httpClient.post('/order-status', payload)
  }

  update (id: number, payload: IOrderStatusForm): Promise<AxiosResponse<IOrderStatusForm>> {
    return httpClient.patch(`/order-status/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/order-status', {
      ids
    })
  }
}

export default new OrderStatusApi()
