import { ICurrencyState } from '@/store/modules/currency/state'
import { MutationTree } from 'vuex'
import { ICurrencyItem, ICurrencyTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent'
}

export const mutations: MutationTree<ICurrencyState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: ICurrencyState, data: IResponseTable<ICurrencyTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: ICurrencyState, data: ICurrencyItem[]) => {
    state.items = data
  }
}
