import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IZoneForm,
  IZoneItem,
  IZoneTableItem,
  IResponseTable
} from '@/types'

class ZoneApi {
  getById (id: number): Promise<AxiosResponse<IZoneForm>> {
    return httpClient.get(`/zone/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IZoneItem[]>> {
    return httpClient.get('/zone', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IZoneTableItem>>> {
    return httpClient.post('/zone/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IZoneForm): Promise<AxiosResponse<IZoneForm>> {
    return httpClient.post('/zone', payload)
  }

  update (id: number, payload: IZoneForm): Promise<AxiosResponse<IZoneForm>> {
    return httpClient.patch(`/zone/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/zone', {
      ids
    })
  }
}

export default new ZoneApi()
