import { IUploadState } from '@/store/modules/upload/state'
import { MutationTree } from 'vuex'
import { IUploadItem, IUploadTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent'
}

export const mutations: MutationTree<IUploadState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IUploadState, data: IResponseTable<IUploadTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IUploadState, data: IUploadItem[]) => {
    state.items = data
  }
}
