import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IFilterGroupForm,
  IFilterGroupItem,
  IFilterGroupTableItem,
  IResponseTable
} from '@/types'

class FilterGroupApi {
  getById (id: number): Promise<AxiosResponse<IFilterGroupForm>> {
    return httpClient.get(`/filter-group/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IFilterGroupItem[]>> {
    return httpClient.get('/filter-group', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IFilterGroupTableItem>>> {
    return httpClient.post('/filter-group/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IFilterGroupForm): Promise<AxiosResponse<IFilterGroupForm>> {
    return httpClient.post('/filter-group', payload)
  }

  update (id: number, payload: IFilterGroupForm): Promise<AxiosResponse<IFilterGroupForm>> {
    return httpClient.patch(`/filter-group/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/filter-group', {
      ids
    })
  }
}

export default new FilterGroupApi()
