import { IAttributeGroupState } from '@/store/modules/attribute-group/state'
import { MutationTree } from 'vuex'
import { IAttributeGroupForm, IAttributeGroupItem, IAttributeTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_FORM_EVENT = 'loadFormEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent'
}

export const mutations: MutationTree<IAttributeGroupState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IAttributeGroupState, data: IResponseTable<IAttributeTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IAttributeGroupState, data: IAttributeGroupItem[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_FORM_EVENT]: (state: IAttributeGroupState, data: IAttributeGroupForm) => {
    state.dataForm = data
  }
}
