import { mutationTypes } from './mutations'
import { ActionTree } from 'vuex'
import { ICustomerState } from '@/store/modules/customer/state'
import CustomerApi from '@/services/api/CustomerApi'

export enum actionTypes {
  LOAD_TABLE = 'loadTable',
  LOAD_ITEMS = 'loadItems',
  LOAD_TABLE_HISTORY = 'loadTableHistory',
  CREATE_HISTORY = 'createHistory',
  LOAD_TABLE_REWARD = 'loadTableReward',
  CREATE_REWARD = 'createReward',
  LOAD_TABLE_TRANSACTION = 'loadTableTransaction',
  CREATE_TRANSACTION = 'createTransaction',
  LOAD_TABLE_IP = 'loadTableIp',
  LOAD_FORM = 'loadForm',
  CREATE_FORM = 'createForm',
  UPDATE_FORM = 'updateForm',
  DELETE_ROWS = 'deleteRows'
}

export const actions: ActionTree<ICustomerState, any> = {
  [actionTypes.LOAD_TABLE]: async ({ commit }, payload: object) => {
    const response = await CustomerApi.getTable(payload || {})

    commit(mutationTypes.LOAD_TABLE_EVENT, response.data)
  },

  [actionTypes.LOAD_TABLE_HISTORY]: async ({ commit }, { id, data }) => {
    const response = await CustomerApi.getTableHistory(id, data)

    commit(mutationTypes.LOAD_TABLE_HISTORY_EVENT, response.data)
  },

  [actionTypes.LOAD_TABLE_REWARD]: async ({ commit }, { id, data }) => {
    const response = await CustomerApi.getTableReward(id, data)

    commit(mutationTypes.LOAD_TABLE_REWARD_EVENT, response.data)
  },

  [actionTypes.LOAD_TABLE_IP]: async ({ commit }, { id, data }) => {
    const response = await CustomerApi.getTableIp(id, data)

    commit(mutationTypes.LOAD_TABLE_IP_EVENT, response.data)
  },

  [actionTypes.LOAD_TABLE_TRANSACTION]: async ({ commit }, { id, data }) => {
    const response = await CustomerApi.getTableTransaction(id, data)

    commit(mutationTypes.LOAD_TABLE_TRANSACTION_EVENT, response.data)
  },

  [actionTypes.LOAD_ITEMS]: async ({ commit }, payload: object) => {
    const response = await CustomerApi.getAll(payload || {})

    commit(mutationTypes.LOAD_ITEMS_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_FORM]: async ({ commit }, id: number) => {
    const response = await CustomerApi.getById(id)

    return response.data
  },

  [actionTypes.CREATE_FORM]: async ({ commit, dispatch }, dataForm) => {
    const response = await CustomerApi.create(dataForm)

    return response.data
  },

  [actionTypes.CREATE_HISTORY]: async ({ commit }, { id, payload }) => {
    const response = await CustomerApi.createHistory(id, payload)
    return response.data
  },

  [actionTypes.CREATE_TRANSACTION]: async ({ commit }, { id, payload }) => {
    const response = await CustomerApi.createTransaction(id, payload)
    return response.data
  },

  [actionTypes.CREATE_REWARD]: async ({ commit }, { id, payload }) => {
    const response = await CustomerApi.createReward(id, payload)
    return response.data
  },

  [actionTypes.UPDATE_FORM]: async ({ commit }, { id, dataForm }) => {
    const response = await CustomerApi.update(id, dataForm)

    return response.data
  },

  [actionTypes.DELETE_ROWS]: async ({ commit, dispatch }, ids: number[]) => {
    await CustomerApi.delete(ids)
  }
}
