import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  ICountryForm,
  ICountryItem,
  ICountryTableItem,
  IResponseTable
} from '@/types'

class CountryApi {
  getById (id: number): Promise<AxiosResponse<ICountryForm>> {
    return httpClient.get(`/country/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<ICountryItem[]>> {
    return httpClient.get('/country', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<ICountryTableItem>>> {
    return httpClient.post('/country/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: ICountryForm): Promise<AxiosResponse<ICountryForm>> {
    return httpClient.post('/country', payload)
  }

  update (id: number, payload: ICountryForm): Promise<AxiosResponse<ICountryForm>> {
    return httpClient.patch(`/country/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/country', {
      ids
    })
  }
}

export default new CountryApi()
