import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  ICustomerForm,
  ICustomerHistoryTableItem,
  ICustomerIpTableItem,
  ICustomerRewardTableItem,
  ICustomerTableItem,
  ICustomerTransactionTableItem,
  IResponseTable
} from '@/types'

class CustomerApi {
  getById (id: number): Promise<AxiosResponse<ICustomerForm>> {
    return httpClient.get(`/customer/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<ICustomerTableItem[]>> {
    return httpClient.get('/customer', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<ICustomerTableItem>>> {
    return httpClient.post('/customer/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: ICustomerForm): Promise<AxiosResponse<ICustomerForm>> {
    return httpClient.post('/customer', payload)
  }

  update (id: number, payload: ICustomerForm): Promise<AxiosResponse<ICustomerForm>> {
    return httpClient.patch(`/customer/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/customer', {
      ids
    })
  }

  getTableTransaction (id: number, payload: any = {}): Promise<AxiosResponse<IResponseTable<ICustomerTransactionTableItem>>> {
    return httpClient.post(`customer/${id}/transaction/paginate`, payload, {
      hideNotification: true
    })
  }

  getTableReward (id: number, payload: any = {}): Promise<AxiosResponse<IResponseTable<ICustomerRewardTableItem>>> {
    return httpClient.post(`customer/${id}/reward/paginate`, payload, {
      hideNotification: true
    })
  }

  getTableIp (id: number, payload: any = {}): Promise<AxiosResponse<IResponseTable<ICustomerIpTableItem>>> {
    return httpClient.post(`customer/${id}/ip/paginate`, payload, {
      hideNotification: true
    })
  }

  getTableHistory (id: number, payload: any = {}): Promise<AxiosResponse<IResponseTable<ICustomerHistoryTableItem>>> {
    return httpClient.post(`customer/${id}/history/paginate`, payload, {
      hideNotification: true
    })
  }

  createHistory (id: number, payload: string): Promise<AxiosResponse> {
    return httpClient.post(`customer/${id}/history`, payload)
  }

  createTransaction (id: number, payload: object): Promise<AxiosResponse> {
    return httpClient.post(`customer/${id}/transaction`, payload)
  }

  createReward (id: number, payload: object): Promise<AxiosResponse> {
    return httpClient.post(`customer/${id}/reward`, payload)
  }
}

export default new CustomerApi()
