import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IReturnStatusForm,
  IReturnStatusItem,
  IReturnStatusTableItem,
  IResponseTable
} from '@/types'

class ReturnStatusApi {
  getById (id: number): Promise<AxiosResponse<IReturnStatusForm>> {
    return httpClient.get(`/return-status/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IReturnStatusItem[]>> {
    return httpClient.get('/return-status', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IReturnStatusTableItem>>> {
    return httpClient.post('/return-status/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IReturnStatusForm): Promise<AxiosResponse<IReturnStatusForm>> {
    return httpClient.post('/return-status', payload)
  }

  update (id: number, payload: IReturnStatusForm): Promise<AxiosResponse<IReturnStatusForm>> {
    return httpClient.patch(`/return-status/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/return-status', {
      ids
    })
  }
}

export default new ReturnStatusApi()
