import { IVoucherThemeState } from '@/store/modules/voucher-theme/state'
import { MutationTree } from 'vuex'
import { IVoucherThemeItem, IVoucherThemeTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IVoucherThemeState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IVoucherThemeState, data: IResponseTable<IVoucherThemeTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IVoucherThemeState, data: IVoucherThemeItem[]) => {
    state.items = data
  }
}
