import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IAttributeForm,
  IAttributeGrouped,
  IAttributeItem,
  IAttributeTableItem,
  IResponseTable
} from '@/types'

class AttributeApi {
  getById (id: number): Promise<AxiosResponse<IAttributeForm>> {
    return httpClient.get(`/attribute/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IAttributeItem[]>> {
    return httpClient.get('/attribute', payload)
  }

  getGrouped (): Promise<AxiosResponse<IAttributeGrouped[]>> {
    return httpClient.get('/attribute/grouped')
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IAttributeTableItem>>> {
    return httpClient.post('/attribute/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IAttributeForm): Promise<AxiosResponse<IAttributeForm>> {
    return httpClient.post('/attribute', payload)
  }

  update (id: number, payload: IAttributeForm): Promise<AxiosResponse<IAttributeForm>> {
    return httpClient.patch(`/attribute/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/attribute', {
      ids
    })
  }
}

export default new AttributeApi()
