const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  const isAuthenticated = vue.$store.getters['user/getAuth']
  console.log(isAuthenticated)
  if ('isAuth' in to.meta && to.meta.isAuth && !isAuthenticated) next('/login')
  else if ('isAuth' in to.meta && !to.meta.isAuth && isAuthenticated) next('/dashboard')
  else next()
})

export default router
