import { IFilterGroupState } from '@/store/modules/filter-group/state'
import { MutationTree } from 'vuex'
import { IFilterGroupForm, IFilterGroupItem, IFilterTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_FORM_EVENT = 'loadFormEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent'
}

export const mutations: MutationTree<IFilterGroupState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IFilterGroupState, data: IResponseTable<IFilterTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IFilterGroupState, data: IFilterGroupItem[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_FORM_EVENT]: (state: IFilterGroupState, data: IFilterGroupForm) => {
    state.dataForm = data
  }
}
