
import { defineComponent, PropType, reactive, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus } from '@/hooks/useFetching'
import { IResponseTable, IUploadTableItem } from '@/types'
import {
  ElMessage,
  FormInstance,
  FormRules,
  UploadInstance,
  UploadUserFile,
  UploadFile,
  UploadRawFile
} from 'element-plus'
import { useConfig } from '@/constants/config'

export interface IPathItem {
  id: number | null;
  name: string
}

export default defineComponent({
  name: 'FileManager',

  setup () {
    const store = useStore()
    const [loadTable, loadStatus] = useFetching((parentId: number | null = null, searchText?: string) => {
      const order = 'uploadType:asc,name:asc'
      const name = searchText
      const payload = parentId
        ? { parentId, order, name }
        : { isNull: 'parentId', order, name }

      return store.dispatch('upload/loadTable', payload)
    })

    const [uploadForm, uploadStatus] = useFetching((fd: FormData) => {
      return store.dispatch('upload/uploadForm', fd)
    })

    const [deleteRows, deleteStatus] = useFetching((ids: number[]) => {
      return store.dispatch('upload/deleteRows', ids)
    })

    const [createForm, createStatus] = useFetching((payload: object) => {
      return store.dispatch('upload/createForm', payload)
    })

    return {
      upload: ref<UploadInstance>(),
      form: ref<FormInstance>(),
      rules: reactive<FormRules>({
        name: [
          { required: true, type: 'string', message: 'is required', trigger: 'blur' }
        ]
      }),
      config: useConfig,
      loadTable,
      loadStatus,
      createForm,
      createStatus,
      deleteRows,
      deleteStatus,
      uploadForm,
      uploadStatus
    }
  },

  props: {
    modelValue: String as PropType<string>,

    visible: Boolean as PropType<boolean>
  },

  watch: {
    localModelValue: function (value) {
      this.$emit('update:modelValue', value)
    },

    localVisible: function (value) {
      this.$emit('update:visible', value)
    },

    modelValue: function (value: string) {
      this.localModelValue = value
    },

    visible: function (value: boolean) {
      this.localVisible = value
    }
  },

  computed: {
    dataTable (): IResponseTable<IUploadTableItem> {
      return this.$store.getters['upload/getDataTable']
    },

    isLoading (): boolean {
      return this.loadStatus === FetchStatus.pending || this.deleteStatus === FetchStatus.pending || this.createStatus === FetchStatus.pending || this.uploadStatus === FetchStatus.pending
    }
  },

  data () {
    return {
      localModelValue: this.modelValue || null,
      localVisible: this.visible || false,
      selected: [],
      currentId: 0 as number,
      previousId: -1 as number,
      path: [
        { id: null, name: 'Home' }
      ] as IPathItem[],
      isEdit: false,
      model: {
        name: '' as string,
        parentId: null as number | null,
        uploadType: 'folder' as string
      },
      searchText: '' as string,
      uploadFiles: [] as UploadUserFile[]
    }
  },

  mounted () {
    this.loadTable()
  },

  methods: {
    isImage (file: UploadFile) {
      return (/image\/[\w]+/gm).test(file.raw?.type || '')
    },

    handleBeforeUpload (rawFile: UploadRawFile) {
      if (!['image/jpeg'].includes(rawFile.type)) {
        ElMessage.error('Picture must be JPG format!')
        return false
      } else if (rawFile.size / 1024 / 1024 > 2) {
        ElMessage.error('Picture size can not exceed 2MB!')
        return false
      }
      return true
    },

    async handlerSave () {
      if (this.model.uploadType === 'file' && this.upload && this.uploadFiles.length > 0) {
        const file = this.uploadFiles[0].raw
        if (!file) {
          return
        }
        const parentId = this.model.parentId ? this.model.parentId + '' : 'null'
        const fd = new FormData()
        fd.append('file', file, file.name)
        fd.append('parentId', parentId)
        await this.uploadForm(fd)
        await this.loadTable(this.path[this.currentId].id)
        this.upload.clearFiles()
        return
      }

      if (this.form) {
        this.form.validate(async (valid) => {
          if (valid) {
            await this.createForm(this.model)
            this.isEdit = false
            await this.loadTable(this.path[this.currentId].id)
          }
        })
      }
    },

    handleRemove (file: UploadFile) {
      this.uploadFiles.splice(this.uploadFiles.indexOf(file), 1)
    },

    handlerSearch () {
      this.loadTable(this.path[this.currentId].id, this.searchText)
    },

    handlerCancel () {
      if (this.upload) {
        this.upload.clearFiles()
      }

      this.model = {
        name: '',
        parentId: null,
        uploadType: 'folder'
      }

      this.isEdit = false
    },

    async handlerBack () {
      if (this.currentId < 1) {
        this.currentId = 0
      }

      if (this.previousId < 0) {
        this.previousId = -1
        return
      }

      await this.loadTable(this.path[this.previousId].id)
      this.path.splice(this.currentId, 1)

      this.previousId--
      this.currentId--
    },

    handlerReload () {
      this.loadTable(this.path[this.currentId].id)
    },

    handlerUpload () {
      this.isEdit = true
      this.model = {
        name: '',
        parentId: this.path[this.currentId].id,
        uploadType: 'file'
      }
    },

    handlerCreateFolder () {
      this.isEdit = true
      this.model = {
        name: '',
        parentId: this.path[this.currentId].id,
        uploadType: 'folder'
      }
    },

    handleBreadcrumb (index: number) {
      const idx = index + 1

      if (this.path.length === idx) {
        return
      }

      this.currentId = index
      this.previousId = this.currentId - 1
      this.path.splice(idx)
      this.loadTable(this.path[this.currentId].id)
    },

    handleSelect (item: IUploadTableItem) {
      if (item && item.uploadType === 'folder') {
        this.previousId++
        this.currentId++
        this.path.push({
          id: item.id,
          name: item.name
        })

        this.loadTable(this.path[this.currentId].id)
      } else {
        this.localModelValue = `${this.config.files}/${item.filename}` // item.filename
        this.localVisible = false
      }
    },

    async handlerDelete () {
      if (this.selected.length > 0) {
        await this.deleteRows(this.selected)
        this.selected = []
        await this.loadTable(this.path[this.currentId].id)
      }
    }
  }
})
