
import { defineComponent, PropType } from 'vue'
import FileManager from '@/components/FileManager/FileManager.vue'
import TestImg from '@/assets/images/mock/test.jpg'
import { useConfig } from '@/constants/config'

export default defineComponent({
  name: 'ElImageInput',

  props: {
    modelValue: String as PropType<string | null>
  },

  components: {
    FileManager
  },

  setup () {
    return {
      config: useConfig
    }
  },

  watch: {
    modelValue: function (value: string) {
      this.model = value
    },

    model: function (value: string) {
      this.$emit('update:modelValue', value)
      this.$emit('change', value)
    }
  },

  data () {
    return {
      model: this.modelValue || null,
      menuShow: true,
      visible: false
    }
  },

  methods: {
    edit () {
      this.visible = true
    },

    clear () {
      this.model = TestImg
    }
  }
})
