import { IVoucherTableItem, IVoucherItem, IResponseTable, IVoucherHistoryTableItem } from '@/types'

export interface IVoucherState {
  items: IVoucherItem[];
  dataTableMain: IResponseTable<IVoucherTableItem>;
  dataTableHistory: IResponseTable<IVoucherHistoryTableItem>;
}

export const initialState = (): IVoucherState => {
  return {
    items: [],

    dataTableMain: {
      total: 0,
      data: []
    },

    dataTableHistory: {
      total: 0,
      data: []
    }
  }
}
