import { ICategoryState } from '@/store/modules/category/state'
import { MutationTree } from 'vuex'
import { ICategoryItem, ICategoryTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
  LOAD_TREE_EVENT = 'loadTreeEvent'
}

export const mutations: MutationTree<ICategoryState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: ICategoryState, data: IResponseTable<ICategoryTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: ICategoryState, data: ICategoryItem[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_TREE_EVENT]: (state: ICategoryState, data: ICategoryItem[]) => {
    state.tree = data
  }
}
