import { IVoucherState } from '@/store/modules/voucher/state'
import { MutationTree } from 'vuex'
import { IVoucherItem, IVoucherTableItem, IResponseTable, IVoucherHistoryTableItem } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_MAIN_EVENT = 'loadTableMainEvent',
  LOAD_TABLE_HISTORY_EVENT = 'loadTableHistoryEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IVoucherState> = {
  [mutationTypes.LOAD_TABLE_MAIN_EVENT]: (state: IVoucherState, data: IResponseTable<IVoucherTableItem>) => {
    state.dataTableMain = data
  },

  [mutationTypes.LOAD_TABLE_HISTORY_EVENT]: (state: IVoucherState, data: IResponseTable<IVoucherHistoryTableItem>) => {
    state.dataTableHistory = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IVoucherState, data: IVoucherItem[]) => {
    state.items = data
  }
}
