import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  ICustomerGroupForm,
  ICustomerGroupItem,
  ICustomerGroupTableItem,
  IResponseTable
} from '@/types'

class CustomerGroupApi {
  getById (id: number): Promise<AxiosResponse<ICustomerGroupForm>> {
    return httpClient.get(`/customer-group/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<ICustomerGroupItem[]>> {
    return httpClient.get('/customer-group', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<ICustomerGroupTableItem>>> {
    return httpClient.post('/customer-group/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: ICustomerGroupForm): Promise<AxiosResponse<ICustomerGroupForm>> {
    return httpClient.post('/customer-group', payload)
  }

  update (id: number, payload: ICustomerGroupForm): Promise<AxiosResponse<ICustomerGroupForm>> {
    return httpClient.patch(`/customer-group/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/customer-group', {
      ids
    })
  }
}

export default new CustomerGroupApi()
