import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IInformationForm,
  IInformationItem,
  IInformationTableItem,
  IResponseTable
} from '@/types'

class InformationApi {
  getById (id: number): Promise<AxiosResponse<IInformationForm>> {
    return httpClient.get(`/information/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IInformationItem[]>> {
    return httpClient.get('/information', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IInformationTableItem>>> {
    return httpClient.post('/information/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IInformationForm): Promise<AxiosResponse<IInformationForm>> {
    return httpClient.post('/information', payload)
  }

  update (id: number, payload: IInformationForm): Promise<AxiosResponse<IInformationForm>> {
    return httpClient.patch(`/information/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/information', {
      ids
    })
  }
}

export default new InformationApi()
