import { ICustomerState } from '@/store/modules/customer/state'
import { MutationTree } from 'vuex'
import { ICustomerForm, ICustomerTableItem, ICustomerHistoryForm, ICustomerIpForm, IResponseTable, ICustomerRewardForm, ICustomerTransactionForm } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
  LOAD_TABLE_IP_EVENT='loadTableIpEvent',
  LOAD_TABLE_REWARD_EVENT='loadTableRewardEvent',
  LOAD_TABLE_HISTORY_EVENT='loadTableHistoryEvent',
  LOAD_TABLE_TRANSACTION_EVENT='loadTableTransactionEvent'
}

export const mutations: MutationTree<ICustomerState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: ICustomerState, data: IResponseTable<ICustomerTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: ICustomerState, data: ICustomerForm[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_TABLE_HISTORY_EVENT]: (state: ICustomerState, data: IResponseTable<ICustomerHistoryForm>) => {
    state.dataTableHistory = data
  },

  [mutationTypes.LOAD_TABLE_IP_EVENT]: (state: ICustomerState, data: IResponseTable<ICustomerIpForm>) => {
    state.dataTableIp = data
  },

  [mutationTypes.LOAD_TABLE_REWARD_EVENT]: (state: ICustomerState, data: IResponseTable<ICustomerRewardForm>) => {
    state.dataTableReward = data
  },

  [mutationTypes.LOAD_TABLE_TRANSACTION_EVENT]: (state: ICustomerState, data: IResponseTable<ICustomerTransactionForm>) => {
    state.dataTableTransaction = data
  }
}
