import { IOrderStatusState } from '@/store/modules/order-status/state'
import { MutationTree } from 'vuex'
import { IOrderStatusItem, IOrderStatusTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IOrderStatusState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IOrderStatusState, data: IResponseTable<IOrderStatusTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IOrderStatusState, data: IOrderStatusItem[]) => {
    state.items = data
  }
}
