import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IReturnReasonForm,
  IReturnReasonItem,
  IReturnReasonTableItem,
  IResponseTable
} from '@/types'

class ReturnReasonApi {
  getById (id: number): Promise<AxiosResponse<IReturnReasonForm>> {
    return httpClient.get(`/return-reason/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IReturnReasonItem[]>> {
    return httpClient.get('/return-reason', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IReturnReasonTableItem>>> {
    return httpClient.post('/return-reason/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IReturnReasonForm): Promise<AxiosResponse<IReturnReasonForm>> {
    return httpClient.post('/return-reason', payload)
  }

  update (id: number, payload: IReturnReasonForm): Promise<AxiosResponse<IReturnReasonForm>> {
    return httpClient.patch(`/return-reason/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/return-reason', {
      ids
    })
  }
}

export default new ReturnReasonApi()
