import Axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios'
import { TOKEN_KEY } from '@/services/TokenResolver'
import vue from '@/main'
import { actionTypes } from '@/store/modules/user/actions'
import { notify } from '@kyvg/vue3-notification'
import { useConfig } from '@/constants/config'

declare module 'axios' {
  interface AxiosRequestConfig {
    hideNotification?: boolean;
  }
}

const axiosInstance: AxiosInstance = Axios.create({
  baseURL: useConfig.value.api
})

axiosInstance.interceptors.request.use(function (request: AxiosRequestConfig) {
  const token = localStorage.getItem(TOKEN_KEY)
  if (token) {
    if (!request.headers) {
      request.headers = {}
    }

    request.headers.Authorization = `Bearer ${token}`
  }
  return request
}, function (error) {
  return Promise.reject(error)
})

axiosInstance.interceptors.response.use(function (response: AxiosResponse) {
  if (response.config.method !== 'get' && !response.config.hideNotification) {
    notify({
      title: vue.$t('main.messages.success'),
      text: vue.$t('main.messages.successRequest'),
      type: 'success'
    })
  }

  return Promise.resolve(response)
}, async function (error) {
  if (error.config && !error.config.__isRetryRequest) {
    if (error.response.status === 401) {
      await vue.$store.dispatch(`user/${actionTypes.LOGOUT}`)
    } else if ([400, 404].includes(error.response.status)) {
      if (!error.response.config.hideNotification) {
        let messages = ''
        if (error.response.data.message && Array.isArray(error.response.data.message)) {
          messages = error.response.data.message.join(';<br>')
        } else {
          messages = error.response.data.message || vue.$t('main.messages.undefinedError')
        }

        notify({
          title: vue.$t('main.messages.error'),
          text: messages,
          type: 'error'
        })
      }
    }
  } else {
    notify({
      title: vue.$t('main.messages.error'),
      text: vue.$t('main.messages.undefinedError')
    })
  }

  return Promise.reject(error)
})

const httpClient = {
  get (url: string, data?: AxiosRequestConfig) {
    return axiosInstance.get(url, data)
  },

  post (url: string, data?: any, config?: AxiosRequestConfig) {
    return axiosInstance.post(url, data, config)
  },

  put (url: string, data?: any, config?: AxiosRequestConfig) {
    return axiosInstance.put(url, data, config)
  },

  patch (url: string, data?: any, config?: AxiosRequestConfig) {
    return axiosInstance.patch(url, data, config)
  },

  delete (url: string, data?: any, config: AxiosRequestConfig = {}) {
    if (data && !config.data) {
      config.data = data
    }

    return axiosInstance.delete(url, config)
  }
}

export { axiosInstance, httpClient }
