import { mutationTypes } from './mutations'
import { ActionTree } from 'vuex'
import { IVoucherState } from '@/store/modules/voucher/state'
import VoucherApi from '@//services/api/VoucherApi'

export enum actionTypes {
  LOAD_TABLE_MAIN = 'loadTableMain',
  LOAD_TABLE_HISTORY = 'loadTableHistory',
  LOAD_ITEMS = 'loadItems',
  LOAD_FORM = 'loadForm',
  CREATE_FORM = 'createForm',
  UPDATE_FORM = 'updateForm',
  DELETE_ROWS = 'deleteRows'
}

export const actions: ActionTree<IVoucherState, any> = {
  [actionTypes.LOAD_TABLE_MAIN]: async ({ commit }, payload: object) => {
    const response = await VoucherApi.getTableMain(payload || {})

    commit(mutationTypes.LOAD_TABLE_MAIN_EVENT, response.data)
  },

  [actionTypes.LOAD_TABLE_HISTORY]: async ({ commit }, { id, payload }) => {
    const response = await VoucherApi.getTableHistory(id, payload || {})

    commit(mutationTypes.LOAD_TABLE_HISTORY_EVENT, response.data)
  },

  [actionTypes.LOAD_ITEMS]: async ({ commit }, payload: object) => {
    const response = await VoucherApi.getAll(payload || {})

    commit(mutationTypes.LOAD_ITEMS_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_FORM]: async ({ commit }, id: number) => {
    const response = await VoucherApi.getById(id)

    return response.data
  },

  [actionTypes.CREATE_FORM]: async ({ commit }, dataForm) => {
    const response = await VoucherApi.create(dataForm)

    return response.data
  },

  [actionTypes.UPDATE_FORM]: async ({ commit }, { id, dataForm }) => {
    const response = await VoucherApi.update(id, dataForm)

    return response.data
  },

  [actionTypes.DELETE_ROWS]: async ({ commit, dispatch }, ids: number[]) => {
    await VoucherApi.delete(ids)
  }
}
