import { IProductState } from '@/store/modules/product/state'
import { MutationTree } from 'vuex'
import { IProductItem, IProductTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IProductState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IProductState, data: IResponseTable<IProductTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IProductState, data: IProductItem[]) => {
    state.items = data
  }
}
