import { GetterTree } from 'vuex'
import { IOptionState } from '@/store/modules/option/state'

export const getters: GetterTree<IOptionState, any> = {
  getTypes: state => state.types,
  getItems: state => state.items,
  getValueItems: state => state.valueItems,
  getDataTable: state => state.dataTable,
  getDataForm: state => state.dataForm
}
