import { IAttributeTableItem, IAttributeForm, IAttributeItem, IAttributeGrouped, IResponseTable } from '@/types'

export interface IAttributeState {
  grouped: IAttributeGrouped[];
  items: IAttributeItem[];
  dataTable: IResponseTable<IAttributeTableItem>;
  dataForm: IAttributeForm;
}

export const initialState = (): IAttributeState => {
  return {
    grouped: [],

    items: [],

    dataTable: {
      total: 0,
      data: []
    },

    dataForm: {
      id: 1,
      attributeGroupId: 1,
      sortOrder: 0,
      name: 'Attribute 1'
    }
  }
}
