import { mutationTypes } from './mutations'
import { ActionTree } from 'vuex'
import { IAuthState } from '@/store/modules/user/state'
import { RequestLoginValue, ResponseProfileValue } from '@/types'
import AuthService from '@/services/api/auth'
import TokenResolver from '@/services/TokenResolver'
import useNotification from '@/hooks/useNotification'

export enum actionTypes {
  SET_AUTH = 'setAuth',
  SET_TOKEN = 'setToken',
  SET_USER = 'setUser',
  LOAD_USER = 'loadUser',
  LOGIN = 'login',
  LOGOUT = 'logout',
}

export const actions: ActionTree<IAuthState, any> = {
  [actionTypes.SET_AUTH]: ({ commit, dispatch }, payload: boolean) => {
    commit(mutationTypes.SET_AUTH, payload)
  },

  [actionTypes.LOAD_USER]: async ({ commit }) => {
    const response = await AuthService.profile()

    commit(mutationTypes.SET_USER, response.data)
  },

  [actionTypes.SET_TOKEN]: ({ commit, dispatch }, payload: string) => {
    commit(mutationTypes.SET_TOKEN, payload)
    commit(mutationTypes.SET_AUTH, true)
  },

  [actionTypes.SET_USER]: ({ commit, dispatch }, payload: ResponseProfileValue) => {
    commit(mutationTypes.SET_USER, payload)
  },

  [actionTypes.LOGIN]: async ({ commit, dispatch }, payload: RequestLoginValue) => {
    const response = await AuthService.login(payload)
    if (response.status === 201) {
      TokenResolver.setToken(response.data.access_token)
      commit(mutationTypes.SET_AUTH, true)
      commit(mutationTypes.SET_TOKEN, response.data.access_token)

      const profileResponse = await AuthService.profile()
      commit(mutationTypes.SET_USER, profileResponse.data)
    } else {
      await useNotification('   .')
    }
  },

  [actionTypes.LOGOUT]: async ({ commit }) => {
    TokenResolver.removeToken()
    commit(mutationTypes.SET_AUTH, false)
    commit(mutationTypes.SET_TOKEN, null)
    commit(mutationTypes.SET_USER, null)
  }
}
