import { IZoneState } from '@/store/modules/zone/state'
import { MutationTree } from 'vuex'
import { IZoneItem, IZoneTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IZoneState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IZoneState, data: IResponseTable<IZoneTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IZoneState, data: IZoneItem[]) => {
    state.items = data
  }
}
