import { IProductStatusState } from '@/store/modules/product-status/state'
import { MutationTree } from 'vuex'
import { IProductStatusItem, IProductStatusTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IProductStatusState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IProductStatusState, data: IResponseTable<IProductStatusTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IProductStatusState, data: IProductStatusItem[]) => {
    state.items = data
  }
}
