import { IOptionState } from '@/store/modules/option/state'
import { MutationTree } from 'vuex'
import { IOptionForm, IOptionItem, IOptionValueItem, IOptionTableItem, IOptionType, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_FORM_EVENT = 'loadFormEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
  LOAD_VALUE_ITEMS_EVENT = 'loadValueItemsEvent',
  LOAD_TYPES_EVENT = 'loadTypesEvent'
}

export const mutations: MutationTree<IOptionState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IOptionState, data: IResponseTable<IOptionTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IOptionState, data: IOptionItem[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_VALUE_ITEMS_EVENT]: (state: IOptionState, data: IOptionValueItem[]) => {
    state.valueItems = data
  },

  [mutationTypes.LOAD_FORM_EVENT]: (state: IOptionState, data: IOptionForm) => {
    state.dataForm = data
  },

  [mutationTypes.LOAD_TYPES_EVENT]: (state: IOptionState, data: IOptionType[]) => {
    state.types = data
  }
}
