import { ILengthClassState } from '@/store/modules/length-class/state'
import { MutationTree } from 'vuex'
import { ILengthClassItem, ILengthClassTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<ILengthClassState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: ILengthClassState, data: IResponseTable<ILengthClassTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: ILengthClassState, data: ILengthClassItem[]) => {
    state.items = data
  }
}
