import { IReturnActionState } from '@/store/modules/return-action/state'
import { MutationTree } from 'vuex'
import { IReturnActionItem, IReturnActionTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IReturnActionState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IReturnActionState, data: IResponseTable<IReturnActionTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IReturnActionState, data: IReturnActionItem[]) => {
    state.items = data
  }
}
