import { IInformationTableItem, IInformationForm, IInformationItem, IResponseTable } from '@/types'

export interface IInformationState {
  items: IInformationItem[];
  dataTable: IResponseTable<IInformationTableItem>;
  dataForm: IInformationForm;
}

export const initialState = (): IInformationState => {
  return {
    items: [],

    dataTable: {
      total: 0,
      data: []
    },

    dataForm: {
      title: '',
      description: '',
      metaTitle: '',
      metaDescription: '',
      metaKeyword: '',
      isActive: true,
      sortOrder: 0
    }
  }
}
