import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IOptionForm,
  IOptionItem,
  IOptionValueItem,
  IOptionTableItem,
  IOptionType,
  IResponseTable
} from '@/types'

class OptionApi {
  getById (id: number): Promise<AxiosResponse<IOptionForm>> {
    return httpClient.get(`/option/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IOptionItem[]>> {
    return httpClient.get('/option', payload)
  }

  getValues (id: number, payload: any = {}): Promise<AxiosResponse<IOptionValueItem[]>> {
    return httpClient.get(`/option/${id}/value`, payload)
  }

  getTypes (): Promise<AxiosResponse<IOptionType[]>> {
    return httpClient.get('/option/type')
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IOptionTableItem>>> {
    return httpClient.post('/option/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IOptionForm): Promise<AxiosResponse<IOptionForm>> {
    return httpClient.post('/option', payload)
  }

  update (id: number, payload: IOptionForm): Promise<AxiosResponse<IOptionForm>> {
    return httpClient.patch(`/option/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/option', {
      ids
    })
  }
}

export default new OptionApi()
