import { IAuthState } from '@/store/modules/user/state'
import { MutationTree } from 'vuex'
import { ResponseProfileValue } from '@/types'

export enum mutationTypes {
  SET_AUTH = 'setAuth',
  SET_USER = 'setUser',
  SET_TOKEN = 'setToken'
}

export const mutations: MutationTree<IAuthState> = {
  [mutationTypes.SET_AUTH]: (state, data: boolean) => {
    state.auth = data
  },

  [mutationTypes.SET_TOKEN]: (state, data: string) => {
    state.token = data
  },

  [mutationTypes.SET_USER]: (state, data: ResponseProfileValue) => {
    state.user = data
  }
}
