import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IUploadForm,
  IUploadItem,
  IUploadTableItem,
  IResponseTable
} from '@/types'

class UploadApi {
  getById (id: number): Promise<AxiosResponse<IUploadForm>> {
    return httpClient.get(`/upload/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IUploadItem[]>> {
    return httpClient.get('/upload', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IUploadTableItem>>> {
    return httpClient.post('/upload/paginate', payload, {
      hideNotification: true
    })
  }

  upload (fd: any): Promise<AxiosResponse<IUploadItem>> {
    return httpClient.post('/upload/upload', fd)
  }

  create (payload: IUploadForm): Promise<AxiosResponse<IUploadItem>> {
    return httpClient.post('/upload', payload)
  }

  update (id: number, payload: IUploadForm): Promise<AxiosResponse<IUploadItem>> {
    return httpClient.patch(`/upload/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/upload', {
      ids
    })
  }
}

export default new UploadApi()
