import { IManufacturerState } from '@/store/modules/manufacturer/state'
import { MutationTree } from 'vuex'
import { IManufacturerItem, IManufacturerTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IManufacturerState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IManufacturerState, data: IResponseTable<IManufacturerTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IManufacturerState, data: IManufacturerItem[]) => {
    state.items = data
  }
}
