import { GetterTree } from 'vuex'
import { ICustomerState } from '@/store/modules/customer/state'

export const getters: GetterTree<ICustomerState, any> = {
  getItems: state => state.items,
  getDataTable: state => state.dataTable,
  getTableIp: state => state.dataTableIp,
  getTableHistory: state => state.dataTableHistory,
  getTableReward: state => state.dataTableReward,
  getTableTransaction: state => state.dataTableTransaction
}
