import { IAttributeState } from '@/store/modules/attribute/state'
import { MutationTree } from 'vuex'
import { IAttributeForm, IAttributeItem, IAttributeGrouped, IAttributeTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_FORM_EVENT = 'loadFormEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
  LOAD_GROUPED_EVENT = 'loadGroupedEvent'
}

export const mutations: MutationTree<IAttributeState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IAttributeState, data: IResponseTable<IAttributeTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IAttributeState, data: IAttributeItem[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_GROUPED_EVENT]: (state: IAttributeState, data: IAttributeGrouped[]) => {
    state.grouped = data
  },

  [mutationTypes.LOAD_FORM_EVENT]: (state: IAttributeState, data: IAttributeForm) => {
    state.dataForm = data
  }
}
