import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IFilterForm,
  IFilterGrouped,
  IFilterItem,
  IFilterTableItem,
  IResponseTable
} from '@/types'

class FilterApi {
  getById (id: number): Promise<AxiosResponse<IFilterForm>> {
    return httpClient.get(`/filter/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IFilterItem[]>> {
    return httpClient.get('/filter', payload)
  }

  getGrouped (): Promise<AxiosResponse<IFilterGrouped[]>> {
    return httpClient.get('/filter/grouped')
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IFilterTableItem>>> {
    return httpClient.post('/filter/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IFilterForm): Promise<AxiosResponse<IFilterForm>> {
    return httpClient.post('/filter', payload)
  }

  update (id: number, payload: IFilterForm): Promise<AxiosResponse<IFilterForm>> {
    return httpClient.patch(`/filter/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/filter', {
      ids
    })
  }
}

export default new FilterApi()
