import { IWeightClassState } from '@/store/modules/weight-class/state'
import { MutationTree } from 'vuex'
import { IWeightClassItem, IWeightClassTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IWeightClassState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IWeightClassState, data: IResponseTable<IWeightClassTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IWeightClassState, data: IWeightClassItem[]) => {
    state.items = data
  }
}
