import { mutationTypes } from './mutations'
import { ActionTree } from 'vuex'
import { ISystemState } from '@/store/modules/system/state'

export enum actionTypes {
  SHOW_SNACKBAR = 'showSnackbar',
  HIDE_SNACKBAR = 'hideSnackbar',
}

export const actions: ActionTree<ISystemState, any> = {
  [actionTypes.SHOW_SNACKBAR]: ({ commit }, payload: string) => {
    commit(mutationTypes.SET_SNACKBAR, payload)
  },
  [actionTypes.HIDE_SNACKBAR]: ({ commit }) => {
    commit(mutationTypes.SET_SNACKBAR, null)
  }
}
