import { ICustomerGroupState } from '@/store/modules/customer-group/state'
import { MutationTree } from 'vuex'
import { ICustomerGroupItem, ICustomerGroupTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<ICustomerGroupState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: ICustomerGroupState, data: IResponseTable<ICustomerGroupTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: ICustomerGroupState, data: ICustomerGroupItem[]) => {
    state.items = data
  }
}
