import { mutationTypes } from './mutations'
import { ActionTree } from 'vuex'
import { ICategoryState } from '@/store/modules/category/state'
import CategoryApi from '@/services/api/CategoryApi'

export enum actionTypes {
  LOAD_TABLE = 'loadTable',
  LOAD_TREE = 'loadTree',
  LOAD_ITEMS = 'loadItems',
  LOAD_FORM = 'loadForm',
  CREATE_FORM = 'createForm',
  UPDATE_FORM = 'updateForm',
  DELETE_ROWS = 'deleteRows'
}

export const actions: ActionTree<ICategoryState, any> = {
  [actionTypes.LOAD_TABLE]: async ({ commit }, payload: object) => {
    const response = await CategoryApi.getTable(payload || {})

    commit(mutationTypes.LOAD_TABLE_EVENT, response.data)
  },

  [actionTypes.LOAD_TREE]: async ({ commit }) => {
    const response = await CategoryApi.getTree()

    commit(mutationTypes.LOAD_TREE_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_ITEMS]: async ({ commit }, payload: object) => {
    const response = await CategoryApi.getAll(payload || {})

    commit(mutationTypes.LOAD_ITEMS_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_FORM]: async ({ commit }, id: number) => {
    const response = await CategoryApi.getById(id)

    return response.data
  },

  [actionTypes.CREATE_FORM]: async ({ commit }, dataForm) => {
    const response = await CategoryApi.create(dataForm)

    return response.data
  },

  [actionTypes.UPDATE_FORM]: async ({ commit }, { id, dataForm }) => {
    const response = await CategoryApi.update(id, dataForm)

    return response.data
  },

  [actionTypes.DELETE_ROWS]: async ({ commit, dispatch }, ids: number[]) => {
    await CategoryApi.delete(ids)
  }
}
