<template>
  <v-dialog v-model="isShow">
    <v-card max-width="400px">
      <v-card-title v-if="title">
        {{ title }}
      </v-card-title>

      <v-card-text v-if="message">
        {{ message }}
      </v-card-text>

      <v-card-actions>
        <v-btn @click="handleConfirm">
          {{ confirmText || 'Confirm' }}
        </v-btn>

        <v-btn color="primary" @click="handleCancel">
          {{ cancelText || 'Cancel' }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { ref, defineProps, defineEmits, defineExpose } from 'vue'

const isShow = ref(true)

const props = defineProps(['title', 'message', 'confirmText', 'cancelText'])
const emit = defineEmits(['confirm', 'cancel'])

const handleConfirm = () => {
  emit('confirm')
  isShow.value = false
}

const handleCancel = () => {
  emit('cancel')
  isShow.value = false
}

defineExpose({
  handleConfirm,
  handleCancel
})
</script>
