import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IVoucherThemeForm,
  IVoucherThemeItem,
  IVoucherThemeTableItem,
  IResponseTable
} from '@/types'

class VoucherThemeApi {
  getById (id: number): Promise<AxiosResponse<IVoucherThemeForm>> {
    return httpClient.get(`/voucher-theme/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IVoucherThemeItem[]>> {
    return httpClient.get('/voucher-theme', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IVoucherThemeTableItem>>> {
    return httpClient.post('/voucher-theme/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IVoucherThemeForm): Promise<AxiosResponse<IVoucherThemeForm>> {
    return httpClient.post('/voucher-theme', payload)
  }

  update (id: number, payload: IVoucherThemeForm): Promise<AxiosResponse<IVoucherThemeForm>> {
    return httpClient.patch(`/voucher-theme/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/voucher-theme', {
      ids
    })
  }
}

export default new VoucherThemeApi()
