import { IOptionTableItem, IOptionForm, IOptionItem, IOptionValueItem, IOptionType, IResponseTable } from '@/types'

export interface IOptionState {
  types: IOptionType[];
  items: IOptionItem[];
  valueItems: IOptionValueItem[];
  dataTable: IResponseTable<IOptionTableItem>;
  dataForm: IOptionForm;
}

export const initialState = (): IOptionState => {
  return {
    types: [],

    items: [],

    valueItems: [],

    dataTable: {
      total: 0,
      data: []
    },

    dataForm: {
      id: 1,
      optionTypeId: null,
      sortOrder: 0,
      name: 'Option 1',
      values: []
    }
  }
}
