import { ICustomerTableItem, ICustomerForm, IResponseTable, ICustomerIpForm, ICustomerRewardForm, ICustomerTransactionForm, ICustomerHistoryForm } from '@/types'

export interface ICustomerState {
  items: ICustomerForm[];
  dataTable: IResponseTable<ICustomerTableItem>;
  dataTableIp: IResponseTable<ICustomerIpForm>
  dataTableReward: IResponseTable<ICustomerRewardForm>
  dataTableTransaction: IResponseTable<ICustomerTransactionForm>
  dataTableHistory: IResponseTable<ICustomerHistoryForm>
}

export const initialState = (): ICustomerState => {
  return {
    items: [],

    dataTable: {
      total: 0,
      data: []
    },
    dataTableIp: {
      total: 0,
      data: []
    },
    dataTableReward: {
      total: 0,
      data: []
    },
    dataTableTransaction: {
      total: 0,
      data: []
    },
    dataTableHistory: {
      total: 0,
      data: []
    }
  }
}
