import { Ref, ref } from 'vue'

export enum FetchStatus {
  pending = 'pending',
  success = 'success',
  error = 'error',
  null = '',
}

export type FinishType = '' | 'pending' | 'error' | 'success'
// R - response type
export type ResponseFetching<R> = [
  (...args: any[]) => R,
  Ref<FinishType>,
  Ref<string>,
]

export type fetchingCallback<R> = (...args: any[]) => Promise<R>

export type FetchType<R> = (callback: fetchingCallback<R>, statusReset?: number) => ResponseFetching<R>;

const useFetching: FetchType<any> = (callback, statusReset = 2000) => {
  const error = ref<string>('')
  const status = ref<FinishType>('')

  const fetching: fetchingCallback<any> = async (...args: any[]): Promise<any> => {
    let response = null

    try {
      status.value = FetchStatus.pending
      // eslint-disable-next-line n/no-callback-literal
      response = await callback(...args)
      status.value = FetchStatus.success
    } catch (e: any) {
      if (e && 'message' in e) {
        error.value = e.message
      }
      status.value = FetchStatus.error
    } finally {
      if (statusReset > 0) {
        // eslint-disable-next-line no-return-assign
        setTimeout(() => status.value = FetchStatus.null, statusReset)
      }
    }

    if (response !== null) {
      return response
    }
  }
  return [fetching, status, error]
}

export default useFetching
