import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  ICurrencyForm,
  ICurrencyItem,
  ICurrencyTableItem,
  IResponseTable
} from '@/types'

class CurrencyApi {
  getById (id: number): Promise<AxiosResponse<ICurrencyForm>> {
    return httpClient.get(`/currency/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<ICurrencyItem[]>> {
    return httpClient.get('/currency', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<ICurrencyTableItem>>> {
    return httpClient.post('/currency/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: ICurrencyForm): Promise<AxiosResponse<ICurrencyForm>> {
    return httpClient.post('/currency', payload)
  }

  update (id: number, payload: ICurrencyForm): Promise<AxiosResponse<ICurrencyForm>> {
    return httpClient.patch(`/currency/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/currency', {
      ids
    })
  }
}

export default new CurrencyApi()
