import vue from '../main'
import { actionTypes } from '@/store/modules/system/actions'

const useNotification = async (text: string, timeout = 3000) => {
  const { dispatch } = vue.$store
  await dispatch(`system/${actionTypes.SHOW_SNACKBAR}`, text)
  setTimeout(() => {
    dispatch(`system/${actionTypes.HIDE_SNACKBAR}`)
  }, timeout)
}

export default useNotification
