import { mutationTypes } from './mutations'
import { ActionTree } from 'vuex'
import { IWeightClassState } from '@/store/modules/weight-class/state'
import WeightClassApi from '@/services/api/WeightClassApi'

export enum actionTypes {
  LOAD_TABLE = 'loadTable',
  LOAD_ITEMS = 'loadItems',
  LOAD_FORM = 'loadForm',
  CREATE_FORM = 'createForm',
  UPDATE_FORM = 'updateForm',
  DELETE_ROWS = 'deleteRows'
}

export const actions: ActionTree<IWeightClassState, any> = {
  [actionTypes.LOAD_TABLE]: async ({ commit }, payload: object) => {
    const response = await WeightClassApi.getTable(payload || {})

    commit(mutationTypes.LOAD_TABLE_EVENT, response.data)
  },

  [actionTypes.LOAD_ITEMS]: async ({ commit }, payload: object) => {
    const response = await WeightClassApi.getAll(payload || {})

    commit(mutationTypes.LOAD_ITEMS_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_FORM]: async ({ commit }, id: number) => {
    const response = await WeightClassApi.getById(id)

    return response.data
  },

  [actionTypes.CREATE_FORM]: async ({ commit }, dataForm) => {
    const response = await WeightClassApi.create(dataForm)

    return response.data
  },

  [actionTypes.UPDATE_FORM]: async ({ commit }, { id, dataForm }) => {
    const response = await WeightClassApi.update(id, dataForm)

    return response.data
  },

  [actionTypes.DELETE_ROWS]: async ({ commit, dispatch }, ids: number[]) => {
    await WeightClassApi.delete(ids)
  }
}
