import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IProductForm,
  IProductItem,
  IProductTableItem,
  IResponseTable
} from '@/types'

class ProductApi {
  getById (id: number): Promise<AxiosResponse<IProductForm>> {
    return httpClient.get(`/product/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IProductItem[]>> {
    return httpClient.get(`/product?${new URLSearchParams(payload).toString()}`)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IProductTableItem>>> {
    return httpClient.post('/product/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IProductForm): Promise<AxiosResponse<IProductForm>> {
    return httpClient.post('/product', payload)
  }

  update (id: number, payload: IProductForm): Promise<AxiosResponse<IProductForm>> {
    return httpClient.patch(`/product/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/product', {
      ids
    })
  }
}

export default new ProductApi()
