import { IInformationState } from '@/store/modules/information/state'
import { MutationTree } from 'vuex'
import { IInformationForm, IInformationItem, IInformationTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_FORM_EVENT = 'loadFormEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent'
}

export const mutations: MutationTree<IInformationState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IInformationState, data: IResponseTable<IInformationTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IInformationState, data: IInformationItem[]) => {
    state.items = data
  },

  [mutationTypes.LOAD_FORM_EVENT]: (state: IInformationState, data: IInformationForm) => {
    state.dataForm = data
  }
}
