import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  IWeightClassForm,
  IWeightClassItem,
  IWeightClassTableItem,
  IResponseTable
} from '@/types'

class WeightClassApi {
  getById (id: number): Promise<AxiosResponse<IWeightClassForm>> {
    return httpClient.get(`/weight-class/${id}`)
  }

  getAll (payload: any = {}): Promise<AxiosResponse<IWeightClassItem[]>> {
    return httpClient.get('/weight-class', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<IWeightClassTableItem>>> {
    return httpClient.post('/weight-class/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: IWeightClassForm): Promise<AxiosResponse<IWeightClassForm>> {
    return httpClient.post('/weight-class', payload)
  }

  update (id: number, payload: IWeightClassForm): Promise<AxiosResponse<IWeightClassForm>> {
    return httpClient.patch(`/weight-class/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/weight-class', {
      ids
    })
  }
}

export default new WeightClassApi()
