import { mutationTypes } from './mutations'
import { ActionTree } from 'vuex'
import { IOptionState } from '@/store/modules/option/state'
import OptionApi from '@/services/api/OptionApi'

export enum actionTypes {
  LOAD_TABLE = 'loadTable',
  LOAD_ITEMS = 'loadItems',
  LOAD_VALUE_ITEMS = 'loadValueItems',
  LOAD_TYPES = 'loadTypes',
  LOAD_FORM = 'loadForm',
  CREATE_FORM = 'createForm',
  UPDATE_FORM = 'updateForm',
  DELETE_ROWS = 'deleteRows'
}

export const actions: ActionTree<IOptionState, any> = {
  [actionTypes.LOAD_TABLE]: async ({ commit }, payload: object) => {
    const response = await OptionApi.getTable(payload || {})

    commit(mutationTypes.LOAD_TABLE_EVENT, response.data)
  },

  [actionTypes.LOAD_TYPES]: async ({ commit }) => {
    const response = await OptionApi.getTypes()

    commit(mutationTypes.LOAD_TYPES_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_ITEMS]: async ({ commit }, payload: object) => {
    const response = await OptionApi.getAll(payload || { fields: 'id,name,optionTypeId' })

    commit(mutationTypes.LOAD_ITEMS_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_VALUE_ITEMS]: async ({ commit }, { id, payload }) => {
    const response = await OptionApi.getValues(id, payload || { fields: 'id,name' })

    commit(mutationTypes.LOAD_VALUE_ITEMS_EVENT, response.data)

    return response.data
  },

  [actionTypes.LOAD_FORM]: async ({ commit }, id: number) => {
    const response = await OptionApi.getById(id)

    commit(mutationTypes.LOAD_FORM_EVENT, response.data)

    return response.data
  },

  [actionTypes.CREATE_FORM]: async ({ commit }, dataForm) => {
    const response = await OptionApi.create(dataForm)

    commit(mutationTypes.LOAD_FORM_EVENT, response.data)

    return response.data
  },

  [actionTypes.UPDATE_FORM]: async ({ commit }, { id, dataForm }) => {
    const response = await OptionApi.update(id, dataForm)

    commit(mutationTypes.LOAD_FORM_EVENT, response.data)

    return response.data
  },

  [actionTypes.DELETE_ROWS]: async ({ commit, dispatch }, ids: number[]) => {
    await OptionApi.delete(ids)
  }
}
