import { IReturnStatusState } from '@/store/modules/return-status/state'
import { MutationTree } from 'vuex'
import { IReturnStatusItem, IReturnStatusTableItem, IResponseTable } from '@/types'

export enum mutationTypes {
  LOAD_TABLE_EVENT = 'loadTableEvent',
  LOAD_ITEMS_EVENT = 'loadItemsEvent',
}

export const mutations: MutationTree<IReturnStatusState> = {
  [mutationTypes.LOAD_TABLE_EVENT]: (state: IReturnStatusState, data: IResponseTable<IReturnStatusTableItem>) => {
    state.dataTable = data
  },

  [mutationTypes.LOAD_ITEMS_EVENT]: (state: IReturnStatusState, data: IReturnStatusItem[]) => {
    state.items = data
  }
}
