import { AxiosResponse } from 'axios'
import { httpClient } from '@/services/httpClient'
import {
  ICategoryForm,
  ICategoryItem,
  ICategoryTableItem,
  IResponseTable
} from '@/types'

class CategoryApi {
  getById (id: number): Promise<AxiosResponse<ICategoryForm>> {
    return httpClient.get(`/category/${id}`)
  }

  getTree (): Promise<AxiosResponse<ICategoryItem[]>> {
    return httpClient.get('/category/tree')
  }

  getAll (payload: any = {}): Promise<AxiosResponse<ICategoryItem[]>> {
    return httpClient.get('/category', payload)
  }

  getTable (payload: any = {}): Promise<AxiosResponse<IResponseTable<ICategoryTableItem>>> {
    return httpClient.post('/category/paginate', payload, {
      hideNotification: true
    })
  }

  create (payload: ICategoryForm): Promise<AxiosResponse<ICategoryForm>> {
    return httpClient.post('/category', payload)
  }

  update (id: number, payload: ICategoryForm): Promise<AxiosResponse<ICategoryForm>> {
    return httpClient.patch(`/category/${id}`, payload)
  }

  delete (ids: number[]): Promise<AxiosResponse<void>> {
    return httpClient.delete('/category', {
      ids
    })
  }
}

export default new CategoryApi()
